import React from 'react';
import { BrowserRouter, Route } from 'react-router-dom'
import PayPal from './PayPal';
import Credit from './Credit';
import {CardElement, StripeProvider, Elements, injectStripe} from 'react-stripe-elements';


class Tees extends React.Component {

	render(){
		  const data = require('../src/white.jpg');
          return <div>
				   <div className="container">
				      <div className="row">
				         <div className="post-preview">
				            <div className="center">
				               <h2>ANECDOTAL</h2>
				               <h3 className="post-subtitle">
				                  T-Shirts
				               </h3>
				               <img src={data} alt="" height="500"></img>
				               <Route path='/tees/paypal' component={PayPal} />
				               <Elements>
				                  <Route path='/tees/credit' component={Credit} />
				               </Elements>
				               <ul className="nav navbar-nav navbar-right">
				                  <li className="pay">
				                     <a href="/tees/paypal">PayPal</a>
				                  </li>
				                  <li className="pay">
				                     <a href="/tees/credit">Credit Card</a>
				                  </li>
				               </ul>
				            </div>
				         </div>
				      </div>
				   </div>
				</div>

  }
}
export default Tees;



